<template>
  <div class="auth-wrapper auth-v2">
    <b-row class="auth-inner m-0">

      <!-- Brand logo-->
      <b-link class="brand-logo">
        <!-- <vuexy-logo /> -->
        <b-img
          fluid
          :src="icono"
          width="30"
        />

        <h2 class="brand-text text-primary ml-1">
          {{ titulo }}
        </h2>
      </b-link>

      <b-col
        lg="8"
        class="d-none d-lg-flex"
      >
        <div class="align-items-center p-5">

          <div class="text-center pt-3">
            <h1 class="text-center text-primary font-weight-bold">
              {{ institucion }}
            </h1>
            <p class="text-center">
              NON BENA BAI ‚ÄúNUESTRO CAMINO SEGURO‚Äù
            </p>
          </div>
          <b-img
            fluid
            :src="imagenFondoLogin"
            alt="Login V2"
            style="opacity: 0.8;"
          />
        </div>
      </b-col>
      <!-- Login-->
      <b-col
        lg="4"
        class="d-flex align-items-center auth-bg px-2 p-lg-5"
      >
        <b-col
          sm="8"
          md="6"
          lg="12"
          class="px-xl-2 mx-auto"
        >
          <b-card-title
            class="mb-1 font-weight-bold"
            title-tag="h2"
          >
            Direcci√≥n de Bienestar Universitario!üëã
          </b-card-title>
          <b-card-text class="mb-2">
            Inicie sesi√≥n en su cuenta y comience la aventura
          </b-card-text>

          <!-- form -->
          <validation-observer
            ref="loginForm"
            #default="{invalid}"
          >
            <b-form
              class="auth-login-form mt-2"
              @submit.prevent="login"
            >
              <!-- email -->
              <b-form-group
                label="Usuario"
                label-for="login-email"
              >
                <validation-provider
                  #default="{ errors }"
                  name="Email"
                  vid="email"
                  rules="required"
                >
                  <b-form-input
                    id="login-email"
                    v-model="userEmail"
                    :state="errors.length > 0 ? false:null"
                    name="login-email"
                  />
                  <small
                    v-if="errors.length>0"
                    class="text-danger"
                  >El campo usuario es requerido</small>
                </validation-provider>
              </b-form-group>

              <!-- forgot password -->
              <b-form-group>
                <div class="d-flex justify-content-between">
                  <label for="login-password">Contrase√±a</label>

                </div>
                <validation-provider
                  #default="{ errors }"
                  name="Password"
                  vid="password"
                  rules="required"
                >
                  <b-input-group
                    class="input-group-merge"
                    :class="errors.length > 0 ? 'is-invalid':null"
                  >
                    <b-form-input
                      id="login-password"
                      v-model="password"
                      :state="errors.length > 0 ? false:null"
                      class="form-control-merge"
                      :type="passwordFieldType"
                      name="login-password"
                      placeholder="Contrase√±a"
                    />
                    <b-input-group-append is-text>
                      <feather-icon
                        class="cursor-pointer"
                        :icon="passwordToggleIcon"
                        @click="togglePasswordVisibility"
                      />
                    </b-input-group-append>
                  </b-input-group>
                  <small
                    v-if="errors.length>0"
                    class="text-danger"
                  >El campo contrase√±a es requerido</small>
                </validation-provider>
              </b-form-group>

              <b-button
                type="submit"
                variant="primary"
                block
                :disabled="invalid"
              >
                Iniciar sesi√≥n
              </b-button>
            </b-form>
          </validation-observer>

        </b-col>
      </b-col>
    <!-- /Login-->
    </b-row>
  </div>
</template>

<script>
/* eslint-disable global-require */
import { ValidationProvider, ValidationObserver } from 'vee-validate'
// import VuexyLogo from '@core/layouts/components/Logo.vue'
import {
  BRow, BCol, BLink, BFormGroup, BFormInput, BInputGroupAppend, BInputGroup, BCardText, BCardTitle, BImg, BForm, BButton, VBTooltip,
} from 'bootstrap-vue'
import useJwt from '@/auth/jwt/useJwt'
import { required, email } from '@validations'
import { togglePasswordVisibility } from '@core/mixins/ui/forms'
import { mapState } from 'vuex'
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'

export default {
  directives: {
    'b-tooltip': VBTooltip,
  },
  components: {
    BRow,
    BCol,
    BLink,
    BFormGroup,
    BFormInput,
    BInputGroupAppend,
    BInputGroup,
    BCardText,
    BCardTitle,
    BImg,
    BForm,
    BButton,
    ValidationProvider,
    ValidationObserver,
  },

  mixins: [togglePasswordVisibility],
  data() {
    return {
      status: '',
      password: '',
      userEmail: '',
      sideImg: require('@/assets/images/layouts/interculturalidad.png'),

      // validation rules
      required,
      email,
    }
  },
  computed: {
    ...mapState('ajustesApp', ['titulo', 'icono', 'imagenFondoLogin', 'institucion']),

    passwordToggleIcon() {
      return this.passwordFieldType === 'password' ? 'EyeIcon' : 'EyeOffIcon'
    },
    imgUrl() {
      return this.sideImg
    },
  },
  methods: {
    login() {
      this.$refs.loginForm.validate().then(success => {
        if (success) {
          useJwt.login({
            usuario: this.userEmail,
            password: this.password,
          })
            .then(response => {
              const { usuario, permissions, roles } = response.data

              const userData = {
                ...usuario,
                roles: [...roles],
                ability: permissions.length ? [{ action: 'read', subject: 'Auth' }, ...permissions] : null,
              }
              useJwt.setToken(response.data.token)
              localStorage.setItem('userData', JSON.stringify(userData))
              this.$ability.update(userData.ability)

              this.$toast({
                component: ToastificationContent,
                position: 'top-right',
                props: {
                  title: `Bienvenido ${usuario.name || usuario.username}`,
                  icon: 'CoffeeIcon',
                  variant: 'success',
                  text: `Ha iniciado sesi√≥n correctamente como ${usuario.name || usuario.role}. ¬°Ahora puedes empezar a explorar!!`,
                },
              })
              if (localStorage.getItem('routeRedirectLaterLogin')) {
                this.$router.push({ name: 'social-convocatorias-list' })
              } else {
                this.$router.replace('/')
              }
            })
        }
      }).catch(() => {
        this.$toast({
          component: ToastificationContent,
          position: 'top-right',
          props: {
            title: 'Usuario y/o contrase√±a incorrecta',
            icon: 'CoffeeIcon',
            variant: 'warning',

          },
        })
      })
    },
  },
}
</script>

<style lang="scss">
@import '@core/scss/vue/pages/page-auth.scss';
</style>
